export default {
	data() {
		return {
			isDelete: false
		};
	},
	props: ["tokenValue", "index"],
	mounted() {
		// console.log("index",this.index);
	},
	methods: {
		clickToken(token) {
			this.isDelete = true;
			setTimeout(() => {
				this.isDelete = false;
				this.$emit("confirm", {
					token,
					index: this.index
				});
			}, 1500);
		}
	}
}
